@import "fonts/fonts.scss";


//&display=swap  -  Добавить на конце подключаемых шрифтов при добавлении через плагин

// Шрифт по умолчанию
$fontFamily: "Gilroy";
$fontSize: 18px;

// Цвет по умолчанию
$mainColor: #fff;

// Минимальная ширина страницы
$minWidth: 320px;
// Максимальная ширина страницы
$maxWidth: 1920px;
// Ширина контейнера
$widthContainer: 1460px;
// Отступы по бокам контейнера
$containerPadding: 20px;

html{
    scroll-behavior: smooth;
}

body{
    background: #0B1D26 url(../img/top-bg.png) top center no-repeat;
}
h1, h2{
    font-family: "Chronicle Display" !important;
    font-weight: 600;
}
*{
    font-family: $fontFamily !important;
    font-weight: 700;
    font-size: $fontSize;
    color: $mainColor;
}
.container{
    max-width: $maxWidth;
    min-width: $minWidth;
    width: $widthContainer;
    padding: 0 $containerPadding;
    margin: 0 auto;
}
.logo{
    text-decoration: none;
    h2{
        font-weight: bold;
        font-size: 32px;
    }
}