@import "common.scss";
@import "reset.scss";
@import "header.scss";
@import "footer.scss";

.top{
    padding-bottom: 420px;
    max-width: 1920px;
    margin: 0 auto;
    &__inner{
        padding: 200px 80px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }
    &__social{
        display: flex;
        align-items: center;
        position: relative;
        left: -80px;
        transform: rotate(90deg);
    }
    &__social-icon{
        width: 20px;
        height: 20px;
        fill: #fff;
        margin-left: 25px;
        transform: rotate(-90deg);
    }
    &__info{
        max-width: 950px;
    }
    &__info-text{
        font-weight: bold;
        color: #FBD784;
        letter-spacing: 6px;
        padding-left: 100px;
        position: relative;
        &::before{
            content: "";
            position: absolute;
            width: 70px;
            height: 2px;
            left: 0;
            top: 50%;
            background: #FBD784;
        }
    }
    &__info-title{
        font-size: 88px;
        margin-top: 30px;
    }
    &__info-btn{
        display: flex;
        align-items: center;
        text-decoration: none;
        margin-top: 40px;
        max-width: 130px;
    }

    &__info-btn-icon{
        width: 16px;
        height: 25px;
        transform: rotate(90deg);
        fill: #fff;
        margin-left: 15px;
    }
    &__scroller{
        display: flex;
    }
    &__scroller-texts{
        text-align: end;
        margin-right: 30px;
    }
    &__scroller-text{
        margin-top: 15px;
    }
    &__scroller-text + &__scroller-text{
        margin-top: 40px;
    }
    &__scroller-line{
        width: 3px;
        height: 230px;
        background: #ffffff81;
        position: relative;
    }
    &__scroller-line-overflow{
        width: 3px;
        height: 60px;
        background: #fff;
    }
}

.instruction__wrapper:nth-of-type(1){
    padding-top: 420px;
}
.instruction{
    padding-bottom: 100px;
    & + &{
        padding-top: 100px;
    }
    &__inner{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__info{
        max-width: 780px;
    }
    &__info-inner{
        margin-left: 150px;
        position: relative;
        z-index: 1;
    }
    &__info-number{
        font-size: 240px;
        margin-bottom: -120px;
        color: #354147;
        opacity: .75;
        z-index: 0;
        position: relative;
    }
    &__info-subtitle{
        padding-left: 96px;
        color: #FBD784;
        position: relative;
        letter-spacing: 6px;
        text-transform: uppercase;
        margin-bottom: 30px;
        &::before{
            content: "";
            width: 70px;
            height: 2px;
            background: #FBD784;
            top: 50%;
            left: 0;
            position: absolute;
        }
    }
    &__info-title{
        font-size: 64px;
        margin-bottom: 30px;
        font-weight: 600;
    }
    &__info-text{
        line-height: 32px;
        margin-bottom: 30px;
    }
    &__info-btn{
        display: flex;
        text-decoration: none;
        align-items: center;
    }
    &__info-btn-text{
        color: #FBD784;
        margin-right: 15px;
    }
    &__info-btn-icon{
        width: 16px;
        height: 24px;
        fill: #FBD784;
    }
}
@import "adaptive.scss";