.header{
    max-width: 1920px;
    margin: 0 auto;
    &__inner{
        padding: 65px 80px 0;
        display: flex;
        justify-content: space-between;
    }
    &__nav-list{
        display: flex;
        align-items: center;
    }
    &__nav-item{
        padding: 0 20px;
        font-weight: 700;
    }
    &__nav-link{
        text-decoration: none;
        font-size: 18px;
    }
    &__account{
        display: flex;
        text-decoration: none;
        align-items: center;
    }
    &__account-icon{
        width: 20px;
        height: 20px;
        fill: #fff;
        margin-right: 10px;
    }
}