@media (max-width: 1500px) {
    .container{
        width: 1000px;
    }

    .top{
        &__info{
            max-width: 600px;
        }
        &__info-title{
            font-size: 57px;
        }
    }

    .instruction{
        &__info{
            max-width: 500px;
        }
        &__info-number{
            font-size: 200px;
        }
        &__info-inner{
            margin-left: 75px;
        }
        &__info-subtitle{
            padding-left: 50px;
            font-size: 16px;
            &::before{
                width: 40px;
            }
        }
        &__info-title{
            font-size: 55px;
        }
        &__image{
            width: 450px;
        }
    }
    
    .footer{
        padding-bottom: 50px;
        &__links-item + &__links-item{
            margin-top: 10px;
        }
    }
}

@media (max-width: 1040px) {
    .container{
        width: 700px;
    }

    *{
        font-size: 16px s;
    }
    
    .top{
        &__inner{
            display: block;
            position: relative;
        }
        &__social{
            transform: none;
            position: absolute;
            left: 0;
            right: 0;
            bottom: -100px;
            justify-content: center;
        }
        &__social-icon{
            transform: none;
        }
        &__info{
            max-width: 100%;
        }
        &__info-title{
            font-size: 55px;
        }
        &__scroller{
            display: none;
        }
    }

    .instruction{
        &__inner{
            display: block;
        }
        &__info{
            max-width: 700px;
        }
        &__info-inner{
            margin: 0 0 50px 150px;
        }
        &__image{
            display: flex;
            margin: 0 auto;
        }
    }

    .footer{
        &__links-left{
            margin-right: 40px;
        }
    }
}

@media (max-width: 740px){
    .container{
        width: 350px;
    }

    *{
        font-size: 14px;
    }

    .logo h2{
        font-size: 20px;
    }

    .header{
        &__inner{
            padding-top: 40px;
            padding: 40px 10px 0;
        }
        &__nav-item{
            padding: 0 15px;
        }
        &__nav-link{
            font-size: 14px;
        }
        &__account-icon{
            margin-right: 3px;
        }
    }

    .top{
        &__inner{
            padding: 200px 40px 0;
        }
        &__info-text{
            letter-spacing: 3px;
            padding-left: 50px;
            &::before{
                width: 35px;
            }
        }
        &__info-title{
            font-size: 40px;
            margin-top: 15px;
        }
        &__info-btn{
            margin-top: 20px;
        }
        &__social-icon{
            margin-left: 20px;
        }
    }

    .instruction{
        padding-bottom: 50px;
        & + &{
            padding-top: 50px;
        }
        &__inner{
            max-width: 300px;
        }
        &__info-inner{
            margin-left: 0;
        }
        &__info-number{
            margin-bottom: -60px;
        }
        &__info-title{
            font-size: 40px;
            margin-bottom: 15px;
        }
        &__info-subtitle{
            font-size: 14px;
            letter-spacing: 3px;
            margin-bottom: 15px;
        }
        &__info-text{
            line-height: 28px;
            margin-bottom: 15px;
        }
        &__image{
            width: 100%;
        }
    }

    .footer{
        padding-top: 50px;

        &__inner{
            display: block;
        }
        &__info{
            padding-bottom: 40px;
            margin-bottom: 30px;
        }
        &__info-subtitle{
            line-height: 28px;
        }
        &__info-text{
            line-height: 28px;
        }
        &__links-left{
            margin-right: 60px;
        }
        &__links-title{
            font-size: 18px;
        }
        &__links-item{
            line-height: 28px;
        }
    }
}
@media (max-width: 390px){
    .container{
        width: 270px;
        padding: 0 10px;
        margin: 0 auto;
    }
    
    .logo h2{
        font-size: 18px;
    }

    .header{
        &__inner{
            padding: 40px 5px 0;
        }
        &__nav-item{
            padding: 0 5px;
        }
        &__nav-link{
            font-weight: 500;
        }
        &__account-text{
            font-size: 14px;
        }
        &__account-icon{
            width: 16px;
            height: 16px;
            margin-right: 2px;
        }
    }

    .top{
        &__inner{
            padding: 100px 10px 0;
        }
    }
}