.footer{
    padding-bottom: 120px;
    padding-top: 100px;
    &__inner{
        display: flex;
        justify-content: space-between;
    }
    &__info{
        position: relative;
    }
    &__info-subtitle{
        max-width: 300px;
        line-height: 32px;
        margin-top: 25px;
    }
    &__info-text{
        color: #354147;
        line-height: 32px;
        font-weight: 500;
        position: absolute;
        bottom: 0;
        left: 0;
    }
    &__links{
        display: flex;
        justify-content: space-between;
    }
    &__links-left{
        margin-right: 200px;
    }
    &__links-title{
        font-size: 24px;
        line-height: 32px;
        color: #FBD784;
        margin-bottom: 24px;
    }
    &__links-item{
        line-height: 32px;
    }
    &__links-item + &__links-item{
        margin-top: 15px;
    }
    &__links-link{
        text-decoration: none;
    }
}
